module.exports = {
    name: 'list',
    data() {
        return {
            batch_flag: true, //true,false
            batch_datas: [],
            batch_ids: [],

            list: this.List || [], //
            fields: this.FieldList || [], //
            selection: this.Selection || false, //
            btn_info: this.BtnInfo || {}
        }
    },
    methods: {
        /**
         * CheckBox
         * @param  {array} val 
         */
        onSelectionChange(val) {
            this.batch_datas = val;

            this.batch_ids = [];
            if (val.length) {
                this.batch_flag = false;
                for (var i = 0; i < val.length; i++) {
                    this.batch_ids.push(val[i].id);
                }
            } else {
                this.batch_flag = true;
            }

            /**
             * CheckBoxID
             */
            this.$emit('onSelectionChange', this.batch_ids, this.batch_datas);
            this.$emit('onSelectionChangeObj', {
                ids: this.batch_ids,
                datas: this.batch_datas
            });
        },



        /**
         * 
         * @param  {object || boolean} user  ,
         * @param  {number} index 
         * @param  {array} list  
         */
        onDelete(data, index, list) {
            var opts = {};
            if (data === true) {
                opts.batch_ids = this.batch_ids;
                opts.batch_datas = this.batch_datas;
            } else {
                opts.data = data;
                opts.index = index;
                opts.list = list;
            }

            /**
             * 
             * 
             * 1
             *     opts.data 
             *     opts.index 
             *     opts.list 
             * 2
             *     opts.batch_ids ID
             *     opts.batch_datas ()
             */
            this.$emit('onDelete', opts);
        },

        /**
         * 
         * @param  {object} row   
         * @param  {number} index 
         * @param  {array} list  
         */
        onGetInfo(row, index, list, type) {
            this.$emit('onGetInfo', {
                row,
                index,
                list,
                type
            });
        },

        /**
         * 
         * 
         * 1
         *     row 
         * 2
         *     row ID
         */
        onUpdateList(row) {
            if (!Array.isArray(row)) {
                this.list.splice(row, 1);
            } else {
                this.list = this.list.filter(function(item, idx) {
                    return row.indexOf(item.id) === -1;
                });
            }
        }
    },

    mounted() {},

    /**
     * 
     * @type {Object}
     */
    props: {
        List: {
            type: Array,
            required: true
        },
        FieldList: {
            type: Array,
            required: true
        },
        BtnInfo: {
            type: Object
        },
        Selection: {
            type: Boolean,
            default: false
        }
    },


    /**
     * 
     * @type {Object}
     */
    watch: {
        List(v) {
            if (v) {
                this.list = v;
            }
        },
        FieldList(v) {
            if (v) {
                this.fields = v;
            }
        },
        Selection(v) {
            this.selection = v;
        },
        BtnInfo(v) {
            this.btn_info = v;
        },
    }
}


