import {
    user as UserApi
} from 'config/request.js';

import {
    FormData
} from 'common/';

module.exports = {
    name: 'user',
    components: {
        FormData
    },
    data() {
        return {
            fields: [{
                key: 'email',
                value: '',
                desc: '',
                label: ''
            }, {
                key: 'username',
                type: 'input',
                value: '',
                desc: '',
                label: ''
            }, {
                key: 'sex',
                type: 'radio',
                value: {
                    default: 2,
                    list: [{
                        value: 1,
                        text: ''
                    }, {
                        value: 2,
                        text: ''
                    }, {
                        value: 3,
                        text: ''
                    }]
                },
                desc: '',
                label: ''
            }, {
                key: 'address',
                type: 'textarea',
                value: '',
                desc: '',
                label: ''
            }, {
                key: 'status',
                type: 'switch',
                value: {
                    default: true,
                    on: '',
                    off: ''
                },
                desc: '',
                label: ''
            }, {
                key: 'cate',
                type: 'select',
                value: {
                    default: 'sanwen',
                    list: [{
                        value: 'jishu',
                        text: ''
                    }, {
                        value: 'sanwen',
                        text: ''
                    }, {
                        value: 'qita',
                        text: ''
                    }]
                },
                desc: '',
                label: ''
            }, {
                key: 'tag',
                type: 'select',
                multiple: true,
                value: {
                    default: ['qianduan', 'houtai'],
                    list: [{
                        value: 'qianduan',
                        text: ''
                    }, {
                        value: 'mysql',
                        text: ''
                    }, {
                        value: 'houtai',
                        text: ''
                    }, {
                        value: 'fuwuqi',
                        text: ''
                    }]
                },
                desc: '',
                label: ''
            }]
        }
    },
    methods: {
        onGetData(opts) {
            console.log(opts);
        }
    },
    mounted() {

    }
}


