import {
    ListData,
    DialogInfo
} from 'common/';

module.exports = {
    name: 'list',
    components: {
        ListData,
        DialogInfo
    },
    data() {
        return {
            list: [],
            fields: [{
                key: 'username',
                label: ''
            }, {
                key: 'email',
                label: ''
            }, {
                key: 'create_time',
                label: '',
                sort: true
            }, {
                key: 'sex',
                label: '',
                formatter: function(item) {
                    return item.sex == 1 ? '' : (item.sex == 2 ? '' : '');
                },
                filter_list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }, {
                    text: '',
                    value: 3
                }],
                filter_method: function(value, item) {
                    return item.sex == value;
                },
                filter_multiple: true,
            }, {
                key: 'status',
                label: '',
                formatter: function(item) {
                    return item.status == 1 ? '' : '';
                },
                filter_list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }],
                filter_method: function(value, item) {
                    return item.status == value;
                },
                filter_multiple: false,
            }],
            btn_info: {
                // show: false,
                label: 'Actions',
                width: 300,
                // delete: false,
                // select: false,
                // update: false,
                list: [{
                    text: '',
                    fn_type: 'access'
                }, {
                    text: '',
                    fn_type: 'status'
                }]
            },

            dialog: {
                title: '',
                show: false,
                data: {},
                fields: [{
                    key: 'email',
                    label: ''
                }, {
                    key: 'username',
                    label: ''
                }, {
                    key: 'sex_text',
                    label: ''
                }, {
                    key: 'birthday',
                    label: ''
                }, {
                    key: 'address',
                    label: ''
                }, {
                    key: 'status_text',
                    label: ''
                }, {
                    key: 'create_time',
                    label: ''
                }]
            }
        }
    },
    methods: {
        /**
         * 
         */
        getList() {
            var data = {};
            this.$$selectUser.call(this, data, (data) => {
                this.list = data.list;
            });
        },

        /**
         * CheckBox
         * 
         */
        onChangeCheckbox(ids, datas) {

        },

        /**
         * CheckBox
         * 
         */
        onChangeBoxObj(opts) {
            console.log(opts);
            this.$message.success('IDS' + opts.ids);
        },

        /**
         * 
         */
        onGetInfo(opts) {
            console.log(opts);
            switch (opts.type) {
                case 'select':
                    this.onDialogUserInfo(opts.row);
                    break;
                case 'update':
                    this.onUpdate(opts.row);
                    break;
                case 'status':
                    this.onStatus(opts.row);
                    break;
                case 'access':
                    this.onAccess(opts.row);
                    break;
            }
        },

        onDialogUserInfo(data) {
            data.sex_text = data.sex === 1 ? '' : (data.sex === 2 ? '' : '');
            data.status_text = data.status === 1 ? '' : (data.status === 2 ? '' : '');
            this.dialog.data = data;
            this.dialog.show = true;
        },


        onUpdate(data) {
            this.$message.success('' + data.username);
        },


        onStatus(data) {
            this.$message.success('' + data.username);
        },

        onAccess(data) {
            this.$message.success('' + data.username);
        },


        /**
         * 
         */
        onDelete(opts) {
            console.log(opts);
            if (opts.index >= 0) {
                this.$message.success(',' + opts.index);
            } else {
                this.$message.success(',IDS' + opts.batch_ids);
            }
        },
    },
    mounted() {
        this.getList();
    },
    watch: {
        '$route' (to, from) {

        }
    }
}


