module.exports = {
    name: 'list',
    data() {
        return {
            user_list: [], //

            batch_id: '', //id
            batch_flag: true, //true,false

            //
            search_data: {
                username: '',
                email: '',
            },

            //
            dialog: {
                show: false,
                user_info: {}
            },

            //
            sex_filters: {
                list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }, {
                    text: '',
                    value: 3
                }],
                multiple: false
            },

            //
            status_filters: {
                list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }],
                multiple: false
            }
        }
    },
    methods: {
        /**
         * 
         * @param  {object} item 
         * @return {string}      
         */
        formatterSex(item) {
            return item.sex == 1 ? '' : (item.sex == 2 ? '' : '');
        },


        /**
         * 
         * @param  {object} item 
         * @return {string}      
         */
        formatterStatus(item) {
            return item.status == 1 ? '' : '';
        },


        /**
         * 
         * @param  {number} value 
         * @param  {object} item  
         * @return {boolean}       true,false
         */
        filterSex(value, item) {
            return item.sex == value;
        },


        /**
         * 
         * @param  {number} value 
         * @param  {object} item  
         * @return {boolean}       true,false
         */
        filterStatus(value, item) {
            return item.status == value;
        },


        /**
         * 
         */
        onSearch() {
            // console.log(this.search_data);

            var sd = {};

            var query = this.$route.query;
            for (var p in query) {
                sd[p] = query[p];
            }

            for (var s in this.search_data) {
                sd[s] = this.search_data[s];

                if (!sd[s]) {
                    delete sd[s];
                }
            }


            this.$router.push({
                path: this.$route.path,
                query: sd
            });
        },


        /**
         * CheckBox
         * @param  {array} val 
         */
        onSelectionChange(val) {
            // console.log(val);
            if (val.length) {
                this.batch_flag = false;
                var ids = [];
                for (var i = 0; i < val.length; i++) {
                    ids.push(val[i].id);
                }
                this.batch_id = ids.join(',');
            } else {
                this.batch_flag = true;
                this.batch_id = '';
            }
        },


        /**
         * 
         * @param {string} field 
         * @param {string} value 
         */
        setPath(field, value) {
            var path = this.$route.path,
                query = Object.assign({}, this.$route.query);

            query[field] = value;

            this.$router.push({
                path: path,
                query: query
            });
        },


        /**
         * 
         * @param  {number} page 
         */
        onChangeCurrentPage(page) {
            this.setPath('page', page);
        },


        /**
         * 
         * @param  {number} size 
         */
        onChangePageSize(size) {
            this.setPath('page_size', size);
        },


        onAccessUser(user, index, list) {
            UserApi.accessUser.call(this, {
                id: user.id
            }, (data) => {
                user.status = user.status == 1 ? 2 : 1;
            });
        },


        /**
         * 
         * @param  {object || boolean} user  ,
         * @param  {number} index 
         * @param  {array} list  
         */
        onDeleteUser(user, index, list) {
            // console.dir(user);

            if (user === true) {
                var id = this.batch_id;
            } else {
                var id = user.id;
            }

            this.$$deleteUser.call(this, {
                id: id
            }, (data) => {
                if (user === true) {
                    this.user_list = this.user_list.filter(function(item, idx) {
                        return id.indexOf(item.id) === -1;
                    });
                } else {
                    list.splice(index, 1);
                }


                this.getList();
            });
        },


        /**
         * 
         * @param  {object} user 
         */
        onEditUser(user) {
            if (user && user.id) {
                this.$router.push('/demo/user/edit?id=' + user.id);
            } else {
                this.$message({
                    showClose: true,
                    message: 'ID',
                    type: 'error'
                });
            }
        },


        /**
         * 
         * @param  {object} user 
         */
        onSelectUser(user) {
            this.dialog.show = true;
            this.dialog.user_info = user;
        },


        /**
         * 
         */
        getList() {
            var data = {};

            var query = this.$route.query;
            for (var k in query) {
                if (this.search_data[k] !== undefined) {
                    this.search_data[k] = query[k];
                    data[k] = query[k];
                }
            }

            this.$$selectUser.call(this, data, (data) => {
                this.user_list = data.list;
            });
        }
    },

    mounted() {
        this.getList();

        //test dialog

        /* setTimeout(() => {
             this.onSelectUser(this.user_list[0]);
         }, 600);*/
    },
    watch: {
        '$route' (to, from) {
            this.getList();
        }
    }
}


