import {
	ajax
} from 'util/';

/**
 * 
 * 
 * 
 * @type {Object}
 */
module.exports = {
	//
	user: {
		/**
		 * 
		 * @param {object} data 
		 * @param {string} data.username 
		 * @param {string} data.password 
		 * @param {function} fn 
		 */
		login(data, fn, errFn) {
			ajax.call(this, 'post', '/Login/login', data, fn, true, errFn);
		},

		/**
		 * 
		 * @param  	{object}   	data 			
		 * @param 	{string} 	data.username 	-
		 * @param 	{string} 	data.email 		-
		 * @param  	{Function} 	fn   			
		 */
		selectUser(data, fn) {
			ajax.call(this, 'get', '/User/selectUser', data, fn);
		},

		/**
		 * 
		 * @param {object}   data 
		 * @param {string} data.id ID-
		 * @param {string} data.username 
		 * @param {string} data.email 
		 * @param {string} data.sex 
		 * @param {string} data.birthday 
		 * @param {string} data.address 
		 * @param {string} data.status 
		 * @param {function} fn   
		 */
		saveUser(data, fn) {
			ajax.call(this, 'post', '/User/saveUser', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param {string} data.id IDID
		 * @param  {Function} fn   
		 */
		deleteUser(data, fn) {
			ajax.call(this, 'post', '/User/deleteUser', data, fn);
		},

		/**
		 * 
		 * @param  {string}   id ID
		 * @param  {Function} fn 
		 */
		findUser(id, fn) {
			ajax.call(this, 'get', '/User/findUser', {
				id: id
			}, fn);
		},


		/**
		 * 
		 * @param  {object}   data 
		 * @param {string} data.old_password 
		 * @param {string} data.password 
		 * @param {string} data.password_confirm 
		 * @param  {Function} fn   
		 */
		updPass(data, fn) {
			ajax.call(this, 'post', '/User/updatePass', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param {string} data.id ID
		 * @param {string} data.login_style 12
		 * @param {string} data.disabled_update_pass ID
		 * @param  {Function} fn   
		 */
		accessUser(data, fn) {
			ajax.call(this, 'post', '/User/accessUser', data, fn);
		}
	},

	/**
	 * 
	 * @type {Object}
	 */
	article: {
		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		selectArticle(data, fn) {
			ajax.call(this, 'get', '/Article/selectArticle', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		saveArticle(data, fn) {
			ajax.call(this, 'post', '/Article/saveArticle', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		deleteArticle(data, fn) {
			ajax.call(this, 'post', '/Article/deleteArticle', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		findArticle(data, fn) {
			ajax.call(this, 'get', '/Article/findArticle', data, fn);
		},
	},


	/**
	 * 
	 * @type {Object}
	 */
	order: {
		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		statisOrder(data, fn) {
			ajax.call(this, 'get', '/Order/statisOrder', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		selectOrder(data, fn) {
			ajax.call(this, 'get', '/Order/selectOrder', data, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		saveOrder(data, fn) {
			ajax.call(this, 'post', '/Order/saveOrder', data, fn);
		},
	},

	/**
	 * 
	 * @type {Object}
	 */
	system: {
		/**
		 * 
		 * @param  {Function} fn 
		 */
		getSetting(fn) {
			ajax.call(this, 'get', '/System/getSetting', {}, fn);
		},

		/**
		 * 
		 * @param  {object}   data 
		 * @param  {Function} fn   
		 */
		updateSetting(data, fn) {
			ajax.call(this, 'post', '/System/updateSetting', data, fn);
		}
	}
};


