<template>
    <div class="list">
        <!-- 
            ref:
            List:
            FieldList:
            Selection:
            @onSelectionChange:CheckBox
            @onDelete:
         -->
        <h2></h2>
        <list-data
            ref='list'
            :List='list'
            :FieldList='fields'
            :Selection='true'
            :BtnInfo='btn_info'
            @onSelectionChange='onChangeCheckbox'
            @onSelectionChangeObj='onChangeBoxObj'
            @onDelete='onDelete'
            @onGetInfo='onGetInfo'></list-data>


        <dialog-info
            :Dialog='dialog'>
        </dialog-info>
    </div>
</template>
<script>
    import ListJs from './List.js';
    module.exports=ListJs;
</script>
<style scoped lang='less'>
    
</style>



